body {
    margin: 0;
    font-family: Intro-Book;
    background-color: #EDF0F7;
}

a {
    text-decoration: none;
}

ul {
    padding: 0;
    margin: 0;
}

li {
    list-style: none;
}

button {
    cursor: pointer;
    outline: none;
    border: none;
}

*,
*:before,
*:after {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

input {
    outline: none;
    cursor: pointer;
    margin: 0;
}